.content{
    margin: 1rem 3rem;
}
    .content .showon{
        padding: 0.5rem 1rem;
        text-align: start;
        height: auto;
        max-height: 3rem;
        width: auto;
        max-width: 20rem;
        z-index: 100;
        position: fixed;
        top: 5rem;
        right: 7rem;
        color: #fff;
        font-size: 0.8rem;
        background-color: rgb(70, 70, 70);
        animation: show_on 500ms ease-in-out;
    }

@keyframes show_on{
    0% {
        top: 6rem;
        opacity: 0;
    }
    100%{
        opacity: 0.9;
    }
}