.ModalCart{
    max-height: 20rem;
    width: 50rem;
    max-width: 90%;
    z-index: 50;
    background-color: #fff;
    position:fixed;
    display: block;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: show_top 500ms ease-in-out;
}
.jsb{
    display: flex !important;
    justify-content: space-between;
}
.elementmodal{
    max-height: 15rem;
    white-space: nowrap;
    overflow-y:scroll;
}
 .Fix{
    cursor: pointer;
    display: block;
    position: relative;
    padding: 1rem 1rem;
    color: #fff;
}
.hfix{
    height: 4rem !important;
}
.elementItem{
    padding: 0.3rem 1rem;
    border-bottom: 1px solid rgb(185, 183, 183);
}
.Fixis{
    position: absolute;
    right: 1rem;
}
.overlay{
    animation: show 500ms ease-out;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: block;
    position: fixed;
    height: 100vh;
    width: 100vw;
    overflow-y:unset;
    z-index: 49;
    background-color: darkgrey;
    opacity: 0.5;
   
}

.cartItems{
    display: grid;
    grid-template-columns: 18rem 1.6rem 2rem 1.6rem 2rem 1fr 2rem;
}
.plus, .minus{
    color: #000;
    font-weight: 900;
    cursor: pointer;
}
.plus{
    font-size: 1.1rem;
    padding: 0 !important;
    vertical-align: middle;
    font-weight: 100;
}


@keyframes show_top {
    0% {
        top: 60%;
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
@keyframes show {
    0% {
        opacity: 0;
    }
    100%{
        opacity: 0.5;
    }
}