.grid-col{
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(auto-fill, minmax(250px,1fr));
}
.grid-col .card-title{
    position: relative;
    background-color: rgb(48, 46, 46);
    opacity: 0.5;
    color: rgb(230, 217, 217) !important;
    width: 100%;
}
.grid-col .rate{
    padding: 0.5rem;
    background-color: rgb(8, 168, 56);
    opacity: 0.5;
    color: #fff !important;
}
.grid-col .card-content{
    height: 4rem !important;
    overflow-y:scroll;
}
.grid-col .card-action{
    display: flex;
    justify-content: space-between;
}
.grid-col .card-action .up{
    padding-top: 0.5rem;
}
    
.cart{
    border-radius: 0.5rem;
    z-index: 100;
    position: fixed;
    bottom: 2rem;
    right: 2rem;
    cursor: pointer;
    padding: 1rem 1rem 0.8rem;
}
.cart.cart-quantity{
    padding-top: 0.5rem;
    vertical-align: 17%;
}
@media (min-width: 767px){
    .cart{
        bottom: unset;
        top: 4rem;
    }
}